import {createSlice} from '@reduxjs/toolkit'
// redux  format

let user = createSlice({
    // key,value
    name : 'user',
    initialState : {name : 'park', age : 20},
    reducers : {
        //   
        changeName(state){
            state.name = 'JJam';
        },
        changeAge(state, action){
            state.age += action.payload;
            // payload    .
        }
    }
})

export let {changeName, changeAge} = user.actions

export default user