// useEffect  ?
//   . return  rendring    useEffect

function Detail(props) {
    let { itemId } = useParams();
    let findItem = props.shoes.find(x => x.id === itemId)
    // let [count, setCount] = useState(0);
    let [alert, setAlert] = useState(true);
    let [tap, setTap] = useState(0);
    let dispatch = useDispatch();

    // useEffect(() => {
    //     // console.log(findItem.itemId);
    //     let value = localStorage.getItem('watched')
    //     value = JSON.parse(value)
    //     value.push(findItem.id)
    //     value = new Set([...value])
    //     value = Array.from(value)
    //     localStorage.setItem('watched', JSON.stringify(value))
    // }, [])
    //  


    useEffect(() => {
        //    
        //mount, update 
        let timer = setTimeout(() => { setAlert(!alert) }, 2000)

        return () => {
            // useEffect   
            //   Timer  
            clearTimeout(timer)
        }
    }, [])


    // id routes 
    return (<div className="container">
        {/* <YellowBtn bg="skyblue"></YellowBtn>
        <YellowBtn bg="blue"></YellowBtn> */}
        {
            alert
                ? <div className="alert alert-warning">
                    2   
                </div>
                : null
        }


        {/* {count}
        <button onClick={() => { setCount(count++) }}></button> */}
        <div className="row">
            <div className="col-md-6">
                <img src={`https://codingapple1.github.io/shop/shoes${props.shoes[itemId].id + 1}.jpg`} width="100%" />
            </div>
            <div className="col-md-6">
                <h4 className="pt-5">{props.shoes[itemId].title}</h4>
                <p>{props.shoes[itemId].content}</p>
                <p>{props.shoes[itemId].price}</p>
                <button className="btn btn-danger" onClick={() => {
                    dispatch(addItem({ id: `${props.shoes[itemId].id}`, name: `${props.shoes[itemId].title}`, count: 0 }))
                }}></button>
            </div>
        </div>

        <Nav variant="tabs" defaultActiveKey="link0">
            <Nav.Item>
                <Nav.Link onClick={() => setTap(0)} eventKey="link0">0</Nav.Link>
            </Nav.Item>
            <Nav.Item>
                <Nav.Link onClick={() => setTap(1)} eventKey="link1">1</Nav.Link>
            </Nav.Item>
            <Nav.Item>
                <Nav.Link onClick={() => setTap(2)} eventKey="link2">2</Nav.Link>
            </Nav.Item>
        </Nav>
        <TabContent tap={tap} shoes={props.shoes}></TabContent>

    </div>
    )
}

function TabContent({ tap, shoes }) {

    let [fade, setFade] = useState('')
    let { stock } = useContext(Context)
    // context api
    // component    
    // state (,  )

    useEffect(() => {
        setTimeout(() => { setFade('end') }, 100)
        return () => {
            setFade('')
        }
    }, [tap])

    return (<div className={`start ${fade}`}>
        {[<div>{shoes[0].title}</div>, <div>1</div>, <div>2</div>][tap]}
    </div>)
}


export default Detail