import { Container, Nav, Navbar, Row, Col } from 'react-bootstrap';


function Card(props) {
    return (
        <Col sm>
            <img src={`https://codingapple1.github.io/shop/shoes${props.shoes.id + 1}.jpg`} width="80%"></img>
            {/* public  /  */}
            <h4>{props.shoes.title}</h4>
            <p>{props.shoes.price}</p>
        </Col>
    )

}

export default Card