import { Table } from "react-bootstrap"
import { useDispatch, useSelector } from "react-redux"
import { changeName, changeAge } from "../store/userSlice"
import { addCount } from "../store"

// redux
function Cart() {

    let tableItems = useSelector((state) => { return state }) //store       
    let dispatch = useDispatch()
    console.log(tableItems)

    return (
        <div>
            <h4>{tableItems.user.age} {tableItems.user.name} </h4>
            <button onClick={() => {
                dispatch(changeAge(1));
                dispatch(changeName())
            }}> </button>
            <Table>
                <thead>
                    <tr>
                        <th>#</th>
                        <th>ID</th>
                        <th></th>
                        <th></th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    {
                        tableItems.tableItems.map((e, i) => {
                            return (
                                <tr ket={i}>
                                    <td>{i}</td>
                                    <td>{e.id}</td>
                                    <td>{e.name}</td>
                                    <td>{e.count}</td>
                                    <td>
                                        <button onClick={()=>{
                                            dispatch(addCount(e.id))
                                        }}>+</button>
                                    </td>
                                </tr>
                            )
                        })
                    }
                </tbody>
            </Table>
        </div>
    )
}

export default Cart